import CollectionPlayer from "@/CollectionPlayer";
import axios from "axios";
import util from "@/util";
import { reactive } from 'vue'

export default class Gallery {
  allCollections: CollectionPlayer[] = reactive([])

  async loadData() {
    // If gallery has already been loaded, return immediately
    if (this.allCollections.length > 0) {
      return
    }

    let response = await axios.get(util.baseUrl() + '/gallery-info.json')
    let sequences = response.data.sequences

    if (sequences == undefined) {
      console.error("Could not load gallery info. Likely badly formatted json.")
      return
    }

    let infoPromises: UnloadedCollection[] = []
    let loadCount = Math.min(3, sequences.length)
    for (let [index, id] of sequences.entries()) {
      // Add requests to the list of promises
      infoPromises.push({id: id, infoPromise: util.getInfo(id)})

      // As soon as we have a few requests, wait for them to resolve so that user will be presented by these quickly
      // Then loadCount is increased so that more and more requests are loaded in parallel.
      if (infoPromises.length >= loadCount || index == sequences.length-1) {
        loadCount *= 2
        await this.createCollections(infoPromises)
        infoPromises = []
      }
    }
  }

  private async createCollections(unloadedCollection: UnloadedCollection[]) {
    let ids: string[] = unloadedCollection.map(value => value.id)
    let infos: any[] = await Promise.all(unloadedCollection.map(value => value.infoPromise))
    for (let i = 0; i < infos.length; i++) {
      let id = ids[i]
      let info = infos[i]
      this.allCollections.push(new CollectionPlayer(id, info))
    }
  }
}

interface UnloadedCollection {
  id: string,
  infoPromise: Promise<any>
}
