
import {Options, setup, Vue} from 'vue-class-component'
import axios from 'axios'
import util from '../util'
import BlushButton from "@/components/BlushButton.vue";
import Hjson from 'hjson'
import PatreonButton from "@/components/PatreonButton.vue";
import CollectionPlayer from "@/CollectionPlayer";
import BlushThumb from "@/components/BlushThumb.vue";

@Options({
  components: {
    BlushButton, PatreonButton, BlushThumb
  },
  props: {
  },
  computed: {
    cssVars() {
      return {
        '--zoom-level': this.zoomLevel
      }
    }
  },
  methods: {
    log (msg: any) {
      console.log(msg)
    },
    async play() {
      await util.wait(200)
      if (this.player.getCurrentIndex() == this.player.getLastIndex()) {
        await util.wait(200) // Stay extra on the last image
        if (this.paused) return
        this.firstB()
        await util.wait(800)
        if (this.paused) return
      }
      this.nextB()
      await util.wait(800)
      if (!this.paused) this.play()
    },
    playB() {
      this.paused = !this.paused
      if (!this.paused) this.play()
    },
    firstB() {
      this.img = this.player.goto(0).get()
      document.getElementById("image-container")?.scrollTo(0, 0)
    },
    lastB() {
      this.img = this.player.goto(this.player.getLastIndex()).get()
      document.getElementById("image-container")?.scrollTo(0, 0)
    },
    prevB() {
      this.img = this.player.prev().get()
      document.getElementById("image-container")?.scrollTo(0, 0)
    },
    nextB() {
      this.img = this.player.next().get()
      document.getElementById("image-container")?.scrollTo(0, 0)
    },
    exitB() {
      this.$router.push('/')
    },
    sidebarB() {
      if (this.sidebarClass == "sidebar-hidden") {
        this.sidebarClass = "sidebar-shown"
        this.mainview = "mainview-small"
      } else {
        this.sidebarClass = "sidebar-hidden"
        this.mainview = "mainview-large"
      }
    },
    zoomB() {
      if (this.zoomLevel == 0) this.zoomLevel = 1
      else this.zoomLevel = 0
    },
    showImage(url:string) {
      this.img = url
    },
    isVideo(url: string) {
      return util.isVideo(url)
    },
    setTrack(index: number) {
      this.img = this.player.gotoTrack(index).get()
      this.description = this.player.getDescription()
      document.getElementById("image-container")?.scrollTo(0, 0)
    },
    currentTitle() {
      if (this.player.getSubtitle() != "") return `${this.player.getTitle()} - ${this.player.getSubtitle()}`
      return this.player.getTitle()
    }
  },
  async beforeCreate() {
    this.id = this.$route.params.id
    this.info = await util.getInfo(this.id)
    this.description = this.info.description
    this.player = new CollectionPlayer(this.id, this.info)
    this.img = this.player.get()
  },
  mounted() {
    util.registerDisqus(this.$route)
  }
})
export default class ImageView extends Vue {
  id!: string
  img = ''
  description = ''
  info: any = {
    title: "",
    sequences: [
      {
        firstImage: ""
      }
    ]
  }
  player: CollectionPlayer = new CollectionPlayer("none", this.info)
  paused: boolean = true
  sidebarClass = "sidebar-shown"
  mainview = "mainview-small"
  zoomLevel = 0
}
